/**
 *
 * Helpful reusable function-like things that will be used to implement
 * styles for components.
 *
 */
.dsc-sheet__container {
  position: fixed;
  z-index: 700;
  display: none;
}

.dsc-sheet__container.show {
  display: block;
}

.dsc-sheet__container.position-top {
  top: 0;
  right: 0;
  left: 0;
}

.dsc-sheet__container.position-bottom {
  bottom: 0;
  right: 0;
  left: 0;
}

.dsc-sheet__container.position-left {
  bottom: 0;
  top: 0;
  left: 0;
}

.dsc-sheet__container.position-right {
  bottom: 0;
  top: 0;
  right: 0;
}

.dsc-sheet__header {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 710;
}

.dsc-sheet__footer {
  position: sticky;
  position: -webkit-sticky;
  bottom: 0;
  z-index: 710;
}

.dsc-sheet__overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 600;
  background-color: rgba(0, 0, 0, 0.5);
}
